    padding: 0;
    margin: 0;
    list-style: none;
    opacity: 0;
    left: 0;
    right: 0;
    text-align: center;
    position: absolute;
    bottom: -3px;
    z-index: 1;
    transition: all 0.5s ease 0s;
  }
  &:hover .icons {
    opacity: 1;
    bottom: 33px;
  }
  .icons li {
    display: inline-block;
    a {
      font-size: 15px;
      line-height: 35px;
      text-align: center;
      height: 35px;
      width: 35px;
      margin: 3px 4px;
      border-radius: 50px;
      display: block;
      position: relative;
      transition: all 0.3s ease-in-out;
      &:hover {
        color: $white;
      }
      &:after,
      &:before {
        content: attr(data-tip);
        color: $white;
        font-size: 10px;
        letter-spacing: 1px;
        line-height: 20px;
        padding: 1px 5px;
        border-radius: 5px;
        white-space: nowrap;
        opacity: 0;
        transform: translateX(-50%);
        position: absolute;
        left: 50%;
        top: -30px;
      }
      &:after {
        content: '';
        height: 15px;
        width: 15px;
        border-radius: 0;
        transform: translateX(-50%) rotate(45deg);
        top: -20px;
        z-index: -1;
      }
      &:hover {
        &:after,
        &:before {
          opacity: 1;
        }
      }
    }
  }
}

