.horizontalMenu {
  html,
  body,
  iframe,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0;
    padding: 0;
    border: 0;
    font: inherit;
    color: #000 !important;
    vertical-align: baseline;
    font-weight: normal;
    font-size: 12px;
    line-height: 18px;
    font-family: Helvetica, sans-serif;
    -webkit-font-smoothing: subpixel-antialiased;
    font-smoothing: antialiased;
    font-smooth: antialiased;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    -webkit-font-smoothing: subpixel-antialiased;
    font-smoothing: subpixel-antialiased;
    font-smooth: subpixel-antialiased;
  }

  .cl {
    clear: both;
  }

  img {
    border: 0 none;
    max-width: 100%;
  }
}

object,
embed,
video {
  border: 0 none;
  max-width: 100%;
}

.horizontalMenu {
  a:focus {
    outline: none;
  }

  &:before {
    content: '';
    display: table;
  }

  &:after {
    content: '';
    display: table;
    clear: both;
  }
}

/* ================== Desktop Main Menu CSS ================== */

.horizontalMenucontainer {
  background-size: cover;
  overflow: hidden;
  background-attachment: fixed;
  background-position: 50% 0;
  background-repeat: no-repeat;
}

.horizontal-main {
  width: 100%;
  position: relative;
}

.horizontal-mainwrapper {
  margin: 0 auto;
}

.desktoplogo {
  padding: 0.75rem;
  margin: 0;
  float: left;

  img {
    width: 100%;
  }
}

.horizontalMenu {
  color: #fff;
  font-size: 14px;
  padding: 0;
  display: block;

  > .horizontalMenu-list {
    text-align: left;
    margin: 0 auto 0 auto;
    width: 100%;
    display: block;
    padding: 0;

    > li {
      text-align: center;
      display: block;
      padding: 0;
      margin: 0;
      float: left;

      > a {
        display: block;
        padding: 16px;
        text-decoration: none;
        position: relative;
        color: #495584;
        border-right: 1px solid #eaedf1;
      }

      &:last-child > a {
        border-right: 0px;
      }

      a.menuhomeicon {
        padding-left: 25px;
        padding-right: 25px;
      }

      > a {
        i {
          display: inline-block;
          font-size: 16px;
          line-height: inherit;
          margin-right: 4px;
          vertical-align: bottom;
        }
        .horizontal-icon {
          font-size: 12px;
          line-height: 1.8;
        }
        .arrow:after {
          border-left: 4px solid rgba(0, 0, 0, 0);
          border-right: 4px solid rgba(0, 0, 0, 0);
          border-top: 4px solid;
          content: '';
          float: right;
          right: 11px;
          height: 0;
          margin: 0 0 0 14px;
          position: absolute;
          text-align: right;
          top: 33px;
          width: 0;
        }
      }

      &.rightmenu {
        float: right;

        a {
          padding: 0px 30px 0px 20px;
          border-right: none;

          i {
            font-size: 15px;
          }
        }

        float: right;

        &:before {
          content: '';
          display: table;
        }
      }
    }
  }
}

/* Desktop Search Bar */

.horizontalMenu-list > li.rightmenu:after {
  content: '';
  display: table;
}

.horizontalMenu > .horizontalMenu-list > li {
  &.rightmenu {
    &:after {
      clear: both;
    }

    > .topmenusearch {
      float: right;
      width: 210px;
      height: 39px;
      position: relative;
      margin: 16px 0px 0px 0px;

      .searchicon {
        -webkit-transition: all 0.7s ease 0s;
        -moz-transition: all 0.7s ease 0s;
        -o-transition: all 0.7s ease 0s;
        transition: all 0.7s ease 0s;
      }

      input {
        width: 100%;
        position: relative;
        float: right;
        top: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        border: 0;
        padding: 0;
        margin: 0;
        text-indent: 15px;
        height: 39px;
        z-index: 2;
        outline: none;
        color: #333;
        background-color: #efefef;
        -webkit-transition: all 0.7s ease 0s;
        -moz-transition: all 0.7s ease 0s;
        -o-transition: all 0.7s ease 0s;
        transition: all 0.7s ease 0s;
        font-size: 12px;

        &::placeholder {
          color: #a9a9a9;
        }

        &:focus {
          color: #333;
          width: 230px;

          ~ {
            .btnstyle .fa {
              color: #000;
              opacity: 1;
            }

            .searchicon {
              opacity: 1;
              z-index: 3;
              color: #ffffff;
            }
          }
        }
      }

      .btnstyle {
        top: 0px;
        position: absolute;
        right: 0;
        bottom: 0;
        width: 40px;
        line-height: 30px;
        z-index: 1;
        cursor: pointer;
        opacity: 0.3;
        color: #333;
        z-index: 1000;
        background-color: transparent;
        border: solid 0px;
        -webkit-transition: all 0.7s ease 0s;
        -moz-transition: all 0.7s ease 0s;
        -o-transition: all 0.7s ease 0s;
        transition: all 0.7s ease 0s;

        .fa {
          line-height: 37px;
          margin: 0;
          padding: 0;
          text-align: center;
        }

        &:hover .fa {
          opacity: 1;
        }
      }
    }

    zoom: 1;
  }

  > {
    ul.sub-menu {
      position: absolute;
      top: 50px;
      z-index: 1000;
      margin: 0px;
      padding: 5px;
      min-width: 190px;
      background-color: #fff;
      box-shadow: 0 10px 15px 0 rgba(104, 113, 123, 0.3);
      border: 1px solid #f0f0f4;

      > li {
        position: relative;
        margin: 0px;
        padding: 0px;
        display: block;

        > {
          a {
            background-image: none;
            color: #76839a;
            border-right: 0 none;
            text-align: left;
            display: block;
            line-height: 22px;
            padding: 8px 12px;
            text-transform: none;
            font-size: 14px;
            letter-spacing: normal;
            border-right: 0px solid;

            &:hover {
              text-decoration: none;
            }

            > i {
              margin-right: 9px;
            }
          }

          ul.sub-menu {
            min-width: 220px;
            position: absolute;
            left: 100%;
            top: 0;
            margin: 0;
            padding: 0;
            list-style: none;
            background-color: #fff;
            box-shadow: 0 10px 15px 0 rgba(104, 113, 123, 0.3);
            border: 1px solid #f0f0f4;

            > li {
              position: relative;
              margin: 0px;
              padding: 0px;
              display: block;

              > {
                a {
                  background-image: none;
                  color: #76839a;
                  border-right: 0 none;
                  text-align: left;
                  display: block;
                  line-height: 22px;
                  padding: 8px 12px;
                  text-transform: none;
                  font-size: 14px;
                  letter-spacing: normal;
                  border-right: 0px solid;

                  &:hover {
                    text-decoration: none;
                  }

                  > i {
                    margin-right: 9px;
                  }
                }

                ul.sub-menu {
                  min-width: 220px;
                  position: absolute;
                  left: 100%;
                  top: 0;
                  margin: 0px;
                  list-style: none;
                  padding: 0px;
                  background-color: #fff;
                  box-shadow: 0 10px 15px 0 rgba(104, 113, 123, 0.3);
                  border: 1px solid #f0f0f4;
                  > li {
                    position: relative;
                    margin: 0px;
                    padding: 0px;
                    display: block;

                    > a {
                      background-image: none;
                      color: #76839a;
                      border-right: 0 none;
                      text-align: left;
                      display: block;
                      line-height: 22px;
                      padding: 8px 12px;
                      text-transform: none;
                      font-size: 13px;
                      letter-spacing: normal;
                      border-right: 0px solid;

                      &:hover {
                        text-decoration: none;
                      }

                      > i {
                        margin-right: 9px;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }

    .horizontal-megamenu {
      width: 100%;
      left: 0px;
      position: absolute;
      top: 50px;
      color: #25252a;
      z-index: 1000;
      margin: 0px;
      text-align: left;
      font-size: 15px;

      .megamenu-content {
        border: 1px solid #e8ebf3;
        background-color: #fff;
        padding: 14px 20px;
        border-radius: 2px;
        box-shadow: 0px 5px 25px rgba(227, 228, 238, 0.2);
      }

      .title {
        padding: 5px 5px 5px 0px;
        font-size: 16px;
        color: #2c2c2c;
        margin: 0px 0px 4px 0px;
        text-align: left;
        font-weight: 600;
      }

      .wsmwnutxt {
        width: 100%;
        font-size: 14px;
        text-align: justify;
        line-height: 22px;
        color: #2c2c2c;
        margin-top: 10px;
      }

      .link-list li {
        display: block;
        text-align: center;
        white-space: nowrap;
        text-align: left;
        padding: 5px 10px 5px 10px;

        a {
          line-height: 18px;
          border-right: none;
          text-align: left;
          padding: 6px 0px;
          background: #fff;
          background-image: none;
          color: #25252a;
          border-right: 0 none;
          display: block;
          font-size: 14px;
        }
      }

      li {
        i {
          margin-right: 5px;
          text-align: center;
          width: 18px;
        }

        a:hover {
          background: transparent;
          text-decoration: none;
        }
      }

      .link-list li i {
        font-size: 12px;
      }

      li i {
        margin-right: 5px;
        text-align: center;
        width: 18px;
      }

      .mrgtop {
        margin-top: 15px;
      }

      .show-grid div {
        padding-bottom: 10px;
        padding-top: 10px;
        background-color: #dbdbdb;
        border: 1px solid #e7e7e7;
        color: #6a6a6a;
        margin: 2px 0px;
        font-size: 13px;
      }

      &.halfmenu {
        width: 30%;
        right: auto;
        left: auto;
      }

      &.halfdiv {
        width: 20%;
        right: auto;
        left: auto;
      }

      .menu_form {
        width: 100%;
        display: block;

        input[type='text'] {
          width: 100%;
          border: 1px solid #e2e2e2;
          color: #000;
          font-size: 13px;
          padding: 8px 5px;
          margin-bottom: 8px;
        }

        textarea {
          width: 100%;
          border: 1px solid #e2e2e2;
          color: #000;
          font-size: 13px;
          padding: 8px 5px;
          margin-bottom: 8px;
          min-height: 122px;
        }

        input {
          &[type='submit'] {
            width: 25%;
            display: block;
            height: 32px;
            float: right;
            border: none;
            margin-right: 15px;
            cursor: pointer;
            background-color: #e1e1e1;
            -webkit-border-radius: 2px;
            -moz-border-radius: 2px;
            border-radius: 2px;
          }

          &[type='button'] {
            width: 25%;
            display: block;
            height: 32px;
            float: right;
            border: none;
            cursor: pointer;
            background-color: #e1e1e1;
            -webkit-border-radius: 2px;
            -moz-border-radius: 2px;
            border-radius: 2px;
          }
        }
      }

      .carousel-inner .item img {
        width: 100%;
      }

      .carousel-control-next,
      .carousel-control-prev {
        opacity: 0.8;
      }

      .carousel-caption {
        bottom: 0px;
        background-color: rgba(0, 0, 0, 0.7);
        font-size: 13px;
        height: 31px;
        left: 0;
        padding: 7px 0;
        right: 0;
        width: 100%;
      }

      .typography-text {
        padding: 0px 0px;
        font-size: 14px;

        ul {
          padding: 0px 0px;
          margin: 0px;
        }

        p {
          text-align: justify;
          line-height: 24px;
          color: #656565;
        }

        ul li {
          display: block;
          padding: 2px 0px;
          line-height: 22px;

          a {
            color: #656565;
          }
        }
      }
    }
  }
}

