/*-----Lists-----*/

.list-group-item.active {
  z-index: 2;
  background-color: #f6f4fb;
  color: $default-color;
  border-color: #d8dde4;
}

.list-group {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  padding-left: 0;
  margin-bottom: 0;
}

.list-group-item-action {
  width: 100%;
  color: #76839a;
  text-align: inherit;
  &:hover,
  &:focus {
    color: #76839a;
    text-decoration: none;
    background-color: #f1f1f9;
  }
  &:active {
    color: #76839a;
    background-color: #e9ecef;
  }
}

.list-group-item {
  position: relative;
  display: block;
  padding: 0.75rem 1.25rem;
  margin-bottom: -1px;
  background-color: $white;
  border: 1px solid #eaedf1;
}

.listorder {
  position: relative;
  padding: 0.75rem 1.25rem;
  margin-bottom: -1px;
  background-color: $white;
  border: 1px solid #eaedf1;
  list-style-type: decimal;
  list-style-position: inside;
}

.listorder1 {
  position: relative;
  padding: 0.75rem 1.25rem;
  margin-bottom: -1px;
  background-color: $white;
  border: 1px solid #eaedf1;
  list-style-type: upper-alpha;
  list-style-position: inside;
}

.listunorder {
  position: relative;
  padding: 0.75rem 1.25rem;
  margin-bottom: -1px;
  background-color: $white;
  border: 1px solid #eaedf1;
  list-style-type: circle;
  list-style-position: inside;
}

.listunorder1 {
  position: relative;
  padding: 0.75rem 1.25rem;
  margin-bottom: -1px;
  background-color: $white;
  border: 1px solid #eaedf1;
  list-style-type: square;
  list-style-position: inside;
}

.list-group-item {
  &:first-child {
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
  }
  &:last-child {
    margin-bottom: 0;
    border-bottom-right-radius: 3px;
    border-bottom-left-radius: 3px;
  }
  &:hover,
  &:focus {
    z-index: 1;
    text-decoration: none;
    border-bottom: 1px solid transparent;
  }
  &.disabled,
  &:disabled {
    color: #c6cad8;
    background-color: $white;
  }
}

.list-group-flush {
  .list-group-item {
    border-right: 0;
    border-left: 0;
    border-radius: 0;
  }
  &:first-child .list-group-item:first-child {
    border-top: 0;
  }
  &:last-child .list-group-item:last-child {
    border-bottom: 0;
  }
}

.list-group-item-primary {
  color: #24426c;
  background-color: #cbdbf2;
  &.list-group-item-action {
    &:hover,
    &:focus {
      color: #24426c;
      background-color: #b7cded;
    }
    &.active {
      color: $white;
      background-color: #24426c;
      border-color: #24426c;
    }
