.list-inline-dots .list-inline-item + .list-inline-item:before {
  content: ' ';
  margin-left: -2px;
  margin-right: 3px;
}

.list-separated-item {
  padding: 1rem 0;
  &:first-child {
    padding-top: 0;
  }
}

.list-group-item {
  &.active .icon {
    color: inherit !important;
  }
  .icon {
    color: #76839a !important;
  }
}

.list-group-transparent .list-group-item {
  background: none;
  border: 0;
  padding: 0.5rem 1rem;
  border-radius: 3px;
  &.active {
    background: #f5f6fb;
    font-weight: 500;
    color: $black;
  }
}

.list-group.lg-alt .list-group-item {
  border: 0;
}

.list.d-flex img {
  width: 60px;
}
