.icons-list {
  list-style: none;
  margin: 0 -1px -1px 0;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  > li {
    -ms-flex: 1 0 3rem;
    flex: 1 0 3rem;
  }
}

.icons-list-wrap {
  overflow: hidden;
}

.icons-list-item {
  text-align: center;
  height: 3rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  border: 1px solid #e9ebfa;
  margin: 4px;
  border-radius: 5px;
  background: #f5f6fb;
  color: #6d7596;
  i {
    font-size: 1.25rem;
  }
}

/*-----Icon input-----*/

.input-icon {
  position: relative;
}

.input-icon-addon {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  min-width: 2.5rem;
  pointer-events: none;
  color: $white;
  &.search-icon {
    right: auto !important;
    left: 0 !important;
    color: #636262 !important;
  }
  &:last-child {
    left: auto;
    right: 0;
  }
}

.icon-absolute {
  position: absolute;
  display: block;
  bottom: 10px;
  right: 10px;
  padding: 20px 0;
  text-align: center;
  border-radius: 50%;
  width: 70px;
  height: 70px;
}

@media (min-width: 991px) and (max-width: 1024px) {
  .icon-absolute {
    display: none;
  }
}

