textarea {
  overflow: auto;
  resize: vertical;
}
/*------- Alignments & values-------*/

.text-monospace {
  font-family: Monaco, Consolas, 'Liberation Mono', 'Courier New', monospace;
}
.text-justify {
  text-align: justify !important;
}
.text-nowrap {
  white-space: nowrap !important;
}
.text-truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.text-start {
  text-align: left !important;
}
.text-end {
  text-align: right !important;
}
.text-center {
  text-align: center !important;
}
@media (min-width: 576px) {
  .text-sm-left {
    text-align: left !important;
  }
  .text-sm-right {
    text-align: right !important;
  }
  .text-sm-center {
    text-align: center !important;
  }
}
@media (min-width: 768px) {
  .text-md-left {
    text-align: left !important;
  }
  .text-md-right {
    text-align: right !important;
  }
  .text-md-center {
    text-align: center !important;
  }
}
@media (min-width: 992px) {
  .text-lg-left {
    text-align: left !important;
  }
  .text-lg-right {
    text-align: right !important;
  }
  .text-lg-center {
    text-align: center !important;
  }
}
@media (min-width: 1280px) {
  .text-xl-left {
    text-align: left !important;
  }
  .text-xl-right {
    text-align: right !important;
  }
  .text-xl-center {
    text-align: center !important;
  }
}
.text-lowercase {
  text-transform: lowercase !important;
}
.text-uppercase {
  text-transform: uppercase !important;
}
.text-capitalize {
  text-transform: capitalize !important;
}
.text-white-transparent {
  color: $white !important;
  opacity: 0.9;
}
.text-white-transparent-1 {
  color: $white !important;
  opacity: 0.5;
}
a.text-primary {
  &:hover,
  &:focus {
    color: #4e12b6 !important;
  }
}
.text-secondary {
  color: $secondary !important;
}
a.text-secondary {
  &:hover,
  &:focus {
    color: $secondary !important;
  }
}
.text-success {
  color: $success !important;
}
a.text-success {
  &:hover,
  &:focus {
    color: #448700 !important;
  }
}
.text-info {
  color: $info !important;
}
a.text-info {
  &:hover,
  &:focus {
    color: #1594ef !important;
  }
}
.text-warning {
  color: $warning !important;
}
a.text-warning {
  &:hover,
  &:focus {
    color: #c29d0b !important;
  }
}
.text-danger {
  color: $danger !important;
}
a.text-danger {
  &:hover,
  &:focus {
    color: #d22827 !important;
  }
}
.text-light {
  color: #ebedef !important;
}
a.text-light {
  &:hover,
  &:focus {
    color: #dae0e5 !important;
  }
}
.text-dark {
  color: #100f15 !important;
}
a.text-dark {
  &:hover,
  &:focus {
    color: #7b6fe0 !important;
  }
}
.text-body {
  color: #495057 !important;
}
.text-muted {
  color: #76839a !important;
}
.text-black-50 {
  color: $black-5 !important;
}
.text-white-50 {
  color: $white-5 !important;
}
.text-hide {
  font: 0/0 a;
  color: transparent;
  text-shadow: none;
  background-color: transparent;
  border: 0;
}
.text-wrap {
  font-size: 14px;
  line-height: 1.66;
  > {
    :first-child {
      margin-top: 0;
    }
    :last-child {
      margin-bottom: 0;
    }
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      margin-top: 1em;
    }
  }
}
