.responsive-navbar.navbar {
  position: relative !important;
  display: -ms-flexbox;
  display: flex;
  top: 65px;
  margin: 0;
  margin-bottom: 0 !important;
  padding: 0;
  .navbar-collapse {
    padding: 5px;
    position: fixed;
    width: 100%;
    background: $white;
    margin-top: 41px;
    z-index: 999;
    box-shadow: 0 3px 9px 0 rgba(44, 46, 47, 0.2);
  }
  .dropdown-menu.dropdown-menu-left {
    left: -1px;
    right: -1px;
    position: absolute;
  }
  .dropdown {
    position: initial;
  }
  .nav-link.icon {
    padding: 9px !important;
    border: 1px solid #eaedf1;
    margin: 3px;
    color: #2a1b4d !important;
    border-radius: 50px;
    height: 2.5rem;
    width: 2.5rem;
    font-size: 1.2rem;
    box-shadow: 0px 2px 3px rgba(4, 4, 7, 0.1);
  }
}

